.container{
    margin: 0;
    width: 100%;
    animation: 2s infinite alternate ease-in-out;
    min-height: 100vh;
    overflow: hidden;
    display: block;
    position: relative;
}
.home_section_header{
    text-transform: uppercase;
    font-family: Andale Mono, monospace;
    font-size:40px;
    color:#5eade6;
}
/* Unit */
button{cursor:pointer;}
.Unit{
  transition: all .4s ease;
  padding: 20px;
  margin: 10px 0;
  background: #faf8f8;
  border-radius: 9px;
  text-align:left;
  overflow:hidden;
}
.Unit:hover{background: #e4e4e4;}

.Unit div{
  text-transform: uppercase;
  font-family: Andale Mono, monospace;
}
.Unit_number{
  font-weight: bolder;
  color: #6192e0;
  padding-right: 15px;
}
.Unit_title{color:#5eade6;}
.Unit_caret{float:right; padding: 0 10px;}

.Unit > .Unit_caret img{transition: all .4s ease;}
.Unit:hover > .Unit_caret img{transform: rotate(180deg);}

button{
  text-decoration: none;
  border: none;
  background-color:white;
  padding:0;
  margin:0;
  width:100%;
}
button:active {
  outline: none;
  border: none;
}
button:focus {outline:0;}

.menu{
  border: 1px #eee solid;
}
.menu button{
  text-transform: uppercase;
  font-family: Andale Mono, monospace;
  transition: all .4s ease;
  text-align:left;
  color:#5eade6;
}
.menu button:hover{
  background-color: rgba(61, 206, 206, 0.192);
}
/*------------Desktop Setup---------------*/
@media only screen and (min-width: 951px) {
  .home_section_header{font-size:40px;}
  .Unit div{display: inline-block;font-size:25px;}
  .Unit > .Unit_caret img{width: 30px;}
  .menu button{font-size:25px;padding: 2%;}
}
/*-------------Tablet setup--------------*/
@media only screen and (min-width: 751px) and (max-width: 950px) {
  .home_section_header{font-size:30px;}
  .Unit div{display: inline-block;font-size:20px;}
  .Unit_text{display: block;width:80%;}
  .Unit > .Unit_caret img{width: 20px;}
  .menu button{font-size:20px; padding: 15px;}
}
/*-------------Mobile setup--------------*/
@media only screen and (max-width: 750px) {
  .home_section_header{font-size:30px;}
  .Unit div{display: inline-block;font-size:20px;}
  .Unit_text{display: block;width:80%;}
  .Unit_number{ width: 100%;}
  .Unit > .Unit_caret img{width: 20px;}
  .menu button{font-size:20px; padding: 15px;}
}